import AboutView from "../view/about.js";
import SectionHeaderView from "../view/section-header.js";
import AboutItemsView from "../view/about-items.js";
import AboutItemView from "../view/about-item.js";
import AboutProfileView from "../view/about-profile.js";
import AboutExperienceView from "../view/about-experience";
import AboutSliderView from "../view/about-slider";
import { render } from "../utils/render.js";
import { Section, AboutItems } from "../data/data.js";

const { ABOUT } = Section;

export default class About {
  constructor(container) {
    this._container = container;
    this._about = new AboutView();
    this._header = new SectionHeaderView(ABOUT.title, ABOUT.subtitle);
    this._items = new AboutItemsView();
  }

  init() {
    render(this._container, this._about);
    render(this._about, this._header);
    render(this._about, this._items);

    AboutItems.map((item) => {
      const aboutItem = new AboutItemView(item);

      switch (item.type) {
        case `profile`:
          render(aboutItem, new AboutProfileView(item));
          break;
        case `experience`:
          render(aboutItem, new AboutExperienceView(item.places));
          break;
        case `slider`:
          render(aboutItem, new AboutSliderView(item.slides));
          break;
      }

      render(this._items, aboutItem);
    });
  }

  setOnScrollView() {
    this._about.getElement().style.opacity =
      window.pageYOffset > "3650" ? 1 : 0;
  }
}
